.imagePreview {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 100%;
    max-height: 500px;
    gap: 1em;
    position: relative;
    display: block;
    margin-bottom: 2rem;
  }
  
  .downloadButton {
    padding: 0.5em 1em;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1em;
    transition: background-color 0.3s ease;
  }
  
  .downloadButton:hover {
    background-color: #45a049;
  }
  
  .imagePreview img {
    max-width: 100%;
    max-height: 500px;
    width: auto;
    height: auto;
    object-fit: contain;
  }
  
  .imagePreview:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }
  
  .filteredImage {
    max-width: 100%;
    max-height: 400px;
    border-radius: 10px;
    transition: filter 0.3s ease, transform 0.3s ease;
  }
  
  .filteredImage:hover {
    transform: scale(1.02);
  }
  