.advancedFilterControls {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1em;
  }
  
  .filterControl {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    margin-top: 0.5em;
  }
  
  label {
    font-weight: bold;
    margin-bottom: 0.2em;
  }
  
  input[type="range"] {
    width: 80%;
    margin-top: 0.2em;
  }
  
  .applyButton {
    padding: 0.6em 1.2em;
    background-color: #007bff;
    color: #ffffff;
    border: none;
    border-radius: 8px;
    font-size: 1rem;
    transition: background-color 0.3s ease, transform 0.2s ease;
    margin-top: 1em;
  }
  
  .applyButton:hover {
    background-color: #0056b3;
    transform: scale(1.03);
  }
  